import React from 'react'
import { Button, Navbar, Nav, Container, Form, FormControl, Row, Col } from 'react-bootstrap';

import {Link, Route, Switch} from 'react-router-dom';

function Picture(){
    return(

    <div className="login login-position">

        {/* <input type="file" accept="image/*" capture="camera" id = "camera"></input> */}

        {/* <input type="file" accept="image/*;capture=camera" id = "camera"></input> */}

        <div class="container">
            <h1>Camera API</h1>
 
            <div class="main-content">
                <p>A demo of the Camera API, currently implemented in Firefox and Google Chrome on Android. Choose to take a picture with your device's camera and a preview will be shown through createObjectURL or a FileReader object (choosing local files supported too).</p>
 
                
                    <input type="file" id="take-picture" accept="image/*" />
                
 
                <h2>Preview:</h2>
                
                    <img src="about:blank" alt="" id="show-picture" />
                
 
                <p id="error"></p>
 
            </div>
 
            <p class="footer">All the code is available in the <a href="https://github.com/robnyman/robnyman.github.com/tree/master/camera-api">Camera API repository on GitHub</a>.</p>
        </div>
 
 
        <script src="js/base.js"></script>

        
              

        

        
    </div>
    );
}
export default Picture;


        